@media (max-width: $max-mobile-width) {
  .nav-slider-container {
    height: 15vw;
    position: absolute;

    opacity: 0;
    pointer-events: none;
    top: 12vw;
    left: -20vw;
    transition: $transition-time;
  }
  .nav-slider-container.tapped {
    opacity: 1;
    pointer-events: inherit;
    transform: translate(20vw, 0);
    background-color: rgb(247, 247, 211);
  }
  .nav-list {
    padding: 15px 10px 15px 10px;
    border-radius: 5px;
    background-color: rgb(247, 247, 211);
  }
  .nav-list > li {
    margin-bottom: 25px;
    &:hover,
    &:focus {
      color: white;
    }
  }

  .nav-slider-container .nav-list-item-title {
    font-size: large;
  }
}
@media (min-width: $max-mobile-width) {
  .nav-slider-container {
    width: 50vw;
    display: block;
  }
  .nav-list {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    align-self: center;
  }
  .nav-list > li {
    width: 100%;
    height: 5vw;
    display: flex;
    justify-content: center;
    align-items: center;
    &:hover,
    &:focus {
      color: white;
    }
  }
  .nav-slider-container .nav-list-item-title {
    background-color: rgb(196, 220, 255);
    font-size: 1vw;
    font-weight: bold;
  }
}
